import React from 'react'
import { Row, Col } from 'reactstrap'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import Select from 'react-select'
import VideoRecorder from 'react-video-recorder'
import { Recorder } from 'react-voice-recorder'
import 'react-voice-recorder/dist/index.css'
import { Input, Button, FormGroup } from 'reactstrap'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}
const colourOptions1 = [
  {
    value: 'Text',
    label: 'Text',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Attachment',
    label: 'Attachment',
    color: '#00B8D9',
    isFixed: true
  },
  {
    value: 'Voice',
    label: 'Voice',
    color: '#0052CC',
    isFixed: true
  },
  {
    value: 'Video',
    label: 'Video',
    color: '#0052CC',
    isFixed: true
  }
]

class ConatactUs extends React.Component {
  state = {
    addTask: '',
    taskStatus: '',
    isCompleted: '',
    taskImportant: '',
    importantTask: '',
    taskStarred: '',
    starTask: '',
    newTask: {
      title: '',
      desc: '',
      tags: [],
      isCompleted: false,
      isImportant: false,
      isStarred: false
    },
    updateLabel: '',
    selectAssetRef: '',
    ptype: '',
    audioDetails: {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    },
    taskToUpdate: ''
  }
  completeTask = (value) => {
    this.setState({ isCompleted: value })
  }
  handleAudioStop = (data) => {
    console.log(data)
    this.setState({ audioDetails: data })
  }
  handleAudioUpload = (file) => {
    console.log(file)
  }
  handleRest = () => {
    const reset = {
      url: null,
      blob: null,
      chunks: null,
      duration: {
        h: 0,
        m: 0,
        s: 0
      }
    }
    this.setState({ audioDetails: reset })
  }
  render() {
    const { newTask, taskTitle, taskDesc, taskToUpdate } = this.state
    return (
      <React.Fragment>
        <h2 className="warning spacing">Sender</h2>
        <Row>
          <Col lg="8" md="6" sm="12">
            <Row className="match-height">
              {/* <PerfectScrollbar>
              </PerfectScrollbar> */}
              <Col md="12">
                <FormGroup className="form-label-group">
                  <Select
                    className="React"
                    classNamePrefix="select"
                    // defaultValue={colourOptions1[0]}

                    ref={this.state.selectAssetRef}
                    name="color"
                    options={colourOptions1}
                    isClearable={true}
                    placeholder="Select Message Type..."
                    onChange={(e) => {
                      this.setState({
                        ptype: e ? e.value : ''
                      })
                    }}
                  />
                </FormGroup>
                <FormGroup>
                  <Input
                    type="text"
                    placeholder="Title"
                    value={
                      this.state.taskToUpdate !== null
                        ? taskTitle
                        : newTask.title
                    }
                    onChange={(e) => {
                      if (this.state.taskToUpdate !== null) {
                        this.setState({
                          taskTitle: e.target.value
                        })
                      } else {
                        this.setState({
                          newTask: {
                            ...this.state.newTask,
                            title: e.target.value
                          }
                        })
                      }
                    }}
                  />
                </FormGroup>
                {this.state.ptype === 'Video' && (
                  <div className="video-recorder">
                    <VideoRecorder
                      onRecordingComplete={(videoBlob) => {
                        // Do something with the video...
                        console.log('videoBlob', videoBlob)
                      }}
                    />
                  </div>
                )}
                {this.state.ptype === 'Voice' && (
                  <div className="voice-recorder">
                    <Recorder
                      record={true}
                      title={'New recording'}
                      audioURL={this.state.audioDetails.url}
                      showUIAudio
                      handleAudioStop={(data) => this.handleAudioStop(data)}
                      handleAudioUpload={(data) => this.handleAudioUpload(data)}
                      handleReset={() => this.handleRest()}
                    />
                  </div>
                )}
                <FormGroup>
                  <Input
                    type="textarea"
                    placeholder="Description"
                    rows="5"
                    value={
                      this.state.taskToUpdate !== null ? taskDesc : newTask.desc
                    }
                    onChange={(e) => {
                      if (this.state.taskToUpdate !== null) {
                        this.setState({
                          taskDesc: e.target.value
                        })
                      } else {
                        this.setState({
                          newTask: {
                            ...this.state.newTask,
                            desc: e.target.value
                          }
                        })
                      }
                    }}
                  />
                </FormGroup>
                <div className="chip-wrapper my-1 d-flex flex-wrap">
                  {this.state.taskToUpdate !== null &&
                  this.state.taskToUpdate?.tags &&
                  this.state.taskToUpdate?.tags.length > 0
                    ? this.renderTags(this.state.taskToUpdate?.tags)
                    : null}
                </div>
                <div
                  className="d-flex justify-content-end"
                  style={{ textAlign: 'right' }}
                >
                  <Button.Ripple
                    outline
                    className="mr-1 mb-1 button-label"
                    color="secondary"
                    onClick={() => {
                      this.state.addTask('close')
                      this.setState({
                        newTask: {
                          title: '',
                          desc: '',
                          tags: [],
                          isCompleted: false,
                          isImportant: false,
                          isStarred: false
                        }
                      })
                    }}
                  >
                    Cancel
                  </Button.Ripple>{' '}
                  <Button.Ripple
                    className="mb-1 button-label"
                    color="warning"
                    onClick={() => {
                      console.log('taskToUpdate', taskToUpdate)
                      //toast.success('Note Added Successfully!!')
                      const { isCompleted, isImportant, isStarred, tags } =
                        taskToUpdate
                      if (this.state.taskToUpdate !== null) {
                        this.state.updateTask(
                          taskToUpdate?._id,
                          taskTitle,
                          taskDesc,
                          taskToUpdate,
                          isCompleted,
                          isImportant,
                          isStarred,
                          tags
                        )
                      } else {
                        this.state.addNewTask(this.state.newTask)
                      }
                      this.state.addTask('close')
                      this.setState({
                        newTask: {
                          title: '',
                          desc: '',
                          tags: [],
                          isCompleted: false,
                          isImportant: false,
                          isStarred: false
                        }
                      })
                    }}
                    disabled={
                      taskTitle?.length > 0 || newTask?.title?.length > 0
                        ? false
                        : true
                    }
                  >
                    {taskToUpdate &&
                    taskToUpdate?.id &&
                    this.state.taskToUpdate !== null
                      ? 'Update'
                      : 'Add'}
                  </Button.Ripple>
                </div>
              </Col>
            </Row>
          </Col>
          <Col lg="4" md="6" sm="12">
            <div className="swipermobile">
              <Swiper {...params}>
                <div
                  style={{
                    backgroundImage: `url(${img1})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img2})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img3})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img4})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
                <div
                  style={{
                    backgroundImage: `url(${img5})`,
                    backgroundSize: 'contain'
                  }}
                ></div>
              </Swiper>
            </div>
          </Col>
        </Row>
      </React.Fragment>
    )
  }
}

export default ConatactUs
